  Josh's Custom CSS Reset
  https://www.joshwcomeau.com/css/custom-css-reset/
*/

*,
*::before,
*::after {
  box-sizing: border-box;
}

* {
  margin: 0;
}

body {
  line-height: 1.5;
  -webkit-font-smoothing: antialiased;
}

img,
picture,
video,
canvas,
svg {
  display: block;
  max-width: 100%;
}

input,
button,
textarea,
select {
  font: inherit;
}

p,
h1,
h2,
h3,
h4,
h5,
h6 {
  overflow-wrap: break-word;
}

p {
  text-wrap: pretty;
}
h1,
h2,
h3,
h4,
h5,
h6 {
  text-wrap: balance;
}

#root,
#__next {
  isolation: isolate;
}
/*reset ends */

:root {
  font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
}

body {
  height: 100vh;
  width: 100vw;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background-color: aqua;
}

dialog[open] {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
  flex-wrap: wrap;
  position: absolute;
  float: left;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
}

form {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}

input {
  border: 1px solid black;
  margin-bottom: 20px;
}

.x {
  align-self: flex-end;
}

::backdrop {
  background-color: rgba(128, 128, 128, 0.75);
}

header {
  display: flex;
  flex-direction: column;
  height: 100px;
  background-color: #bcb147;
  color: white;
  width: 100%;
  align-items: center;
  justify-content: center;
  font-size: 2rem;
  font-weight: bold;
}

content {
  display: flex;
  justify-content: center;
  height: 100%;
  width: 100%;
}

#main {
  flex-shrink: 2;
  width: 75%;
  height: 100%;
  background-color: #48bcb0;
  display: grid;
  grid-template-columns: repeat(3, calc(80% / 3));
  grid-template-rows: repeat(3, calc(80% / 3));
  gap: 5%;
  padding-top: 3%;
  justify-content: center;
  justify-items: stretch;
}

aside {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  flex-shrink: 1;
  width: 25%;
  height: 100%;
  background-color: #56e2d4;
}

button {
  padding: 10px 20px;
  border-radius: 0.5rem;
  border: none;
  background-color: #48bcb0;
}

#new-book {
  margin-top: 50px;
}

.book {
  padding: 15px;
  background-color: #70e9dd;
  border: none;
  border-radius: 1rem;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.title {
  font-style: italic;
  font-weight: bold;
}

.book button {
  width: 50%;
  min-width: 90px;
  gap: 10px;
}

.buttons-div {
  display: flex;
  gap: 10px;
}
